import photo from './img.jpeg';
import {SOBRE_MI, PUBLICACIONES, CONFERENCIAS, PAPER_ENTRADAS, CONFERENCIAS_ENTRADAS} from './Constants'

function Paper(props){
    return <>
        <div class='PaperDiv'>
          
            <img src={props.img}></img>
            <div class='Paper'>
                <div class='title'> {props.year} </div>
                <div class='title'> {props.title} </div>
                <div class='authors'> {props.autors} </div>
                <div class='journal'> {props.journal} </div>
                <a class='doi' href={props.doi}> {props.doi} </a>
                <a class='link' href={props.link}> {props.link} </a>
            </div>
        </div>
        </>
}

function Conference(props){
    return  <>
        <div class='PaperDiv'>
            {/* <img src={props.img}></img> */}
            <div class='Paper'>
                <div class='title'> {props.year} </div>
                <div class='title'> {props.title} </div>
                <div class='authors'> {props.autors} </div>
                <div class='journal'> {props.event} </div>
                <div class='place'> {props.place} </div>
                <a class='doi' href={props.doi}> {props.doi} </a>
            </div>
        </div>
    </>;
}

function Papers(){
    let entries=[]
    let papersEntry = PAPER_ENTRADAS.sort((a, b) => a.year > b.year ? -1 : a.year < b.year ? 1 : 0)
    papersEntry.map(entry => (
        entries.push(
                <Paper  img={entry.img}
                        year={entry.year}
                        journal={entry.journal}
                        title={entry.title}
                        autors={entry.autors}
                        doi={entry.doi}
                        link={entry.link}
                />
        )
    ));

    return  <div class='papers'>
                {entries}
            </div>;
}

function Conferences(){
    let entries=[]
    let conferentsEntry = CONFERENCIAS_ENTRADAS.sort((a, b) => a.year > b.year ? -1 : a.year < b.year ? 1 : 0)

    conferentsEntry.map(entry => (
        entries.push(
                <Conference 
                        img={entry.img} 
                        year={entry.year}
                        event={entry.event}
                        title={entry.title}
                        autors={entry.autors}
                        place={entry.place}
                        doi={entry.doi}
                />
        )
    ));
    return  <div class='papers'>
        {entries}
    </div>;
}

