import './App.css';
import MenuEntry from './MenuEntry'
import DivEntry from './DivEntry'
import {PRINCIPAL, SOBRE_MI, PUBLICACIONES, CONFERENCIAS} from './Constants'
import React, { useState } from 'react';

function App() {

  

  const [menu_select, setMenu] = useState("Principal");
  const [class_name, setClassName] = useState(undefined);


  return (
    <>
    <div className="App">
      <div className="Menu">
          <MenuEntry activeMenu={menu_select} menuName={PRINCIPAL} menuCallback={()=>{ 
            setMenu(PRINCIPAL)
            setClassName(undefined)
          }} />  
          <MenuEntry activeMenu={menu_select} menuName={SOBRE_MI} menuCallback={()=>{ 
            setMenu(SOBRE_MI)
            setClassName("App-header-nomain")
          }}/>
          <MenuEntry activeMenu={menu_select} menuName={PUBLICACIONES} menuCallback={()=>{ 
            setMenu(PUBLICACIONES)
            setClassName("App-header-nomain")
          }}/>
          <MenuEntry activeMenu={menu_select} menuName={CONFERENCIAS} menuCallback={()=>{ 
            setMenu(CONFERENCIAS)
            setClassName("App-header-nomain")
          }}/>  
      </div> 
      <header className={"App-header " + class_name} >
        <p> Navarro J. Pablo </p>
      </header>
      <DivEntry activeMenu={menu_select}/>        
    </div>

    </>

  );
}

export default App;
