
 const MenuEntry = (props) => {
    const active = props.activeMenu
    const menu_name = props.menuName
    const menu_callback = props.menuCallback
  
    if (active===menu_name){
      return <p onClick={menu_callback}
        className="SelectMenu">
        {active}
        </p>
    }
    return <p onClick={menu_callback}>
        {menu_name}
        </p>
  }
  export default MenuEntry